@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@200&display=swap);
* {
  margin: 0;
  color: #333;
  font-family: "Josefin Sans", sans-serif;
}

.navbar {
  padding: 20px;
  display: flex;
  align-items: center;
  max-width: 600px;
  margin: 0 auto;
  border-bottom: 1px solid #f2f2f2;
}

.navbar h1 {
  color: #267de0;
}

.navbar .links {
  margin-left: auto;
}

.navbar .link {
  margin-left: 16px;
  text-decoration: none;
  padding: 6px;
}

.navbar .blog-link {
  color: white;
  background-color: #267de0;
  border-radius: 10px;
}

.navbar .link:hover {
  color: #9accfa;
}

.content {
  max-width: 600px;
  margin: 40px auto;
  padding: 20px;
}

.blog-preview {
  padding: 10px 16px;
  margin: 20px 0;
  border-bottom: 1px solif #fafafa;
}

.blog-preview:hover {
  box-shadow: 1px 3px 5px rgba(0, 0, 0, 0.1);
}

.blog-preview h2 {
  font-size: 20px;
  color: #267de0;
  margin-bottom: 8px;
}

.blog-preview a {
  text-decoration: none;
}

.blog-details h2 {
  font-size: 20px;
  color: #267de0;
  margin-bottom: 10px;
}

.blog-details div {
  margin: 20px 0;
}

.blog-details button {
  background: #267de0;
  color: #fff;
  border: 0;
  padding: 8px;
  border-radius: 10px;
  cursor: pointer;
}

.loader {
  display: flex;
  align-items: center;
  flex-direction: column;
  height: 100px;
}

.create {
  max-width: 400px;
  margin: 0 auto;
  text-align: center;
}

.create label {
  text-align: left;
  display: block;
}

.create h2 {
  font-size: 20px;
  color: #267de0;
  margin-bottom: 30px;
}

.create input,
.create textarea,
.create select {
  width: 100%;
  padding: 6px 10px;
  margin: 10px 0;
  border: 1px solid #ddd;
  box-sizing: border-box;
  display: block;
}

.create button {
  background: #267de0;
  color: #fff;
  border: 0;
  padding: 8px;
  border-radius: 10px;
  cursor: pointer;
}

.not-found {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.not-found h2 {
  color: #267de0;
  margin-bottom: 20px;
}

.not-found p {
  margin: 10px;
}

